<template>
<v-snackbar v-model="networkStatusChanges" :timeout="3000" :color="color" >
	<v-layout justify-space-around align-center>
	<v-icon v-if="!this.$store.state.global.offline" style="font-size:35px;color : green">wifi</v-icon>
	<v-icon v-else style="font-size:35px;color : red">wifi_off</v-icon>
	<div style="font-size:24px">{{this.$store.state.global.offline ? "Offline" : "Online" }}</div>
	</v-layout>
</v-snackbar>
</template>
<script>
export default {
  name: "OnlineOffline",
  data() {
    return {
      color: "#0097c1"
    };
  },
  computed: {
    networkStatusChanges: {
      get() {
        return this.$store.state.global.networkStatusChanges;
      },
      // eslint-disable-next-line
      set(v) {}
    }
  }
};
</script>
<style scoped>
</style>



